import appVideo from "../../page-component/app-video/app-video.vue";



import { mapState, mapGetters } from 'vuex';

export default {
    components: {
        'app-video': appVideo,



    },
    props: {
        picList: {
            type: Array,
            default() {
                return []
            }
        },
        share: {
            type: Number
        },
        isCart: {
            type: Boolean,
            default() {
                return true;
            }
        },
        videoUrl: {
            type: String
        },
        goods_id: {
            type: [Number, String]
        },
        sign: {
            type: String
        }
    },
    data() {
        return {

            autoplay: true,




        };
    },
    methods: {
        bannerChange() {
            this.$event.trigger(this.$const.EVENT_VIDEO_END);
        },
        videoStart(videoStatus) {

            videoStatus ? this.autoplay = false : this.autoplay = true;

        },
        preventD() {},
        clickImage(index) {
            let urls = [];
            this.picList.forEach(item => {
                urls.push(item.pic_url);
            });
            uni.previewImage({
                current: index,
                urls: urls
            });
        },

        routeJumpT() {
            uni.navigateTo({
                url: `/pages/goods/video?goods_id=${this.goods_id}&sign=${this.sign}`
            })
        }

    },
    computed: {
        ...mapState({

            mallConfig: state => state.mallConfig,

            isScanQrCode: state => state.page.isScanQrCode
        }),

        ...mapGetters('mallConfig',{
            getVideo: 'getVideo'
        }),

    },

}
